/** Import theme styles */

@media print {
  aside,
  header,
  footer {
    display: none;
  }
}

.main-nav-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.burgermenu {
  display: inline-block;
}

.burgermenu-icon {
  max-height: 20px;
}

.nav-tool-icon {
  max-height: 20px;
}

.menu-item {
  display: inline-block;
}

.current-menu-item {
  border-bottom: 5px solid #65a3d0;
}

.brand-name {
  display: none;
}

.nav-separator {
  display: none;
}

.nav-tool-search {
  text-align: right;
}

.nav-primary {
  width: 100%;
  text-align: center;
}

.nav-language-tool {
  width: 50%;
  text-align: center;
}

.nav-login-tool {
  width: 50%;
  text-align: center;
}

/* Tablets and bigger */

@media screen and (min-width: 769px) {
  .main-nav-container {
    flex-wrap: nowrap;
    justify-content: flex-start;
  }

  .burgermenu {
    display: none;
  }

  .brand-name {
    display: inline-block;
  }

  .nav-primary {
    width: auto;
    text-align: left;
  }

  .nav-tool-search {
    order: 1;
  }

  .nav-language-tool {
    width: auto;
    text-align: left;
    order: 2;
  }

  .nav-login-tool {
    width: auto;
    text-align: left;
    order: 3;
  }

  .nav-icons-label {
    display: none;
  }
}

/* Desktop and bigger */

@media screen and (min-width: 970px) {
  .nav-separator {
    display: inline-block;
  }
}

/* HD Desktop */

@media screen and (min-width: 1200px) {
  .nav-icons-label {
    display: inline-block;
  }
}




